.gridWrapper {
    min-height: 100vh;
    display: grid;
    grid-template: minmax(50px, auto) 1fr minmax(50px, auto) / 1fr;
    grid-template-areas: "header" "main" "footer";
}

.main {
    grid-area: main;
    display: grid;
    grid-template: 1fr / minmax(100px, auto) 1fr;
    grid-template-areas: "navBar content";
}

.navBar {
    grid-area: navBar;
    background-color: greenyellow;
}

.content {
    grid-area: content;
    background-color: #ccf8fb;
    /*background-image: url("assets/images/contentBackground.jpg");*/
}